import { useState } from "react";
import "./styles.css";

export default function App() {
  const [otpo, setOtpo] = useState("");

  if ("OTPCredential" in window) {
    const ac = new AbortController();
    console.log("in if condition otpweb API");
    navigator.credentials
      .get({
        otp: { transport: ["sms"] },
        signal: ac.signal
      })
      .then((otp) => {
        setOtpo(otp.code);
        console.log("otp access complited in if");
        // ac.abort();
      })
      .catch((err) => {
        console.log("in webOtp error");
        // ac.abort();
        console.log(err);
      });
  }
  return (
    <div className="App">
      <h1>Hello CodeSandbox</h1>
      <h2>Your otp {otpo}</h2>
      <input
        type="text"
        value={otpo}
        onChange={(e) => setOtpo(e.target.value)}
      />
    </div>
  );
}
